<template>
  <div class="demo-app-main">
    <!--  -->
    <div style="marginbottom: 20px">
      <span></span>
      <button @click="qiehuanZhou('prev')"></button>
      <button @click="qiehuanZhou('next')"></button>
      <button @click="qiehuanZhou('today')"></button>
    </div>

    <FullCalendar
      ref="fullCalendar"
      class="demo-app-calendar"
      :options="calendarOptions"
    ></FullCalendar>
  </div>
</template>

<script>
import FullCalendar from "@fullcalendar/vue";
import timeGridPlugin from "@fullcalendar/timegrid";
import cnLocale from "@fullcalendar/core/locales/zh-cn";

import moment from "moment";
import tippy from "tippy.js";
import "tippy.js/dist/tippy.css";
import { comp2Dom } from "./util";
import Tooltip from "./Tooltip.vue";

export default {
  components: {
    FullCalendar, // make the <FullCalendar> tag available
  },

  data: function () {
    return {
      calendarOptions: {
        plugins: [
          timeGridPlugin, // timeGridWeek
        ],
        allDaySlot: false, // all-day
        headerToolbar: false, // 
        initialView: "timeGridWeek", // 
        initialEvents: [
          {
            id: 100,
            title: "",
            classNames: "base warning",
            textColor: "#333",
            start: Date.now(),
            end: Date.now() + 3600000 * 3,
            extendedProps: {
              : "07:00",
              : "",
              : "HR",
              : "",
              : "",
              : "",
            },
          },
          {
            id: 101,
            title: "2",
            classNames: "base danger",
            textColor: "#333",
            start: Date.now(),
            end: Date.now() + 3600000 * 2,
            extendedProps: {
              : "07:00",
              : "2",
              : "HR2",
              : "2",
              : "2",
              : "",
            },
          },
          {
            id: 102,
            title: "3",
            classNames: "base info",
            textColor: "#333",
            start: Date.now(),
            end: Date.now() + 3600000 * 1,
            extendedProps: {
              : "07:00",
              : "3",
              : "HR3",
              : "3",
              : "3",
              : "",
            },
          },
          {
            id: 103,
            title: "4",
            classNames: "base success",
            textColor: "#333",
            start: Date.now(),
            end: Date.now() + 3600000 * 4,
            extendedProps: {
              : "07:00",
              : "4",
              : "HR4",
              : "4",
              : "4",
              : "",
            },
          },
        ], // 
        slotEventOverlap: false, // 
        firstDay: 1, // 1 = 
        locale: cnLocale, // 

        dayHeaderFormat: {
          weekday: "short",
          month: "numeric",
          day: "numeric",
          omitCommas: true,
        }, // format
        slotDuration: "00:15:00", // 
        slotLabelInterval: "01:00", // 
        slotMinTime: "00:00:00", // 
        slotMaxTime: "24:00:00", // 
        scrollTime: "08:00:00", // 
        // format
        slotLabelFormat(info) {
          const date = moment("00:00:00", "HH:mm")
            .add(info.date.hour, "hours")
            .add(info.date.minute, "minutes");
          return date.format("HH:mm");
        },

        // 
        eventMouseEnter(info) {
          info.el.parentNode.classList.add("width-all");
        },

        // 
        eventMouseLeave(info) {
          info.el.parentNode.classList.remove("width-all");
        },

        // 
        eventDidMount: function (info) {
          info.el.parentNode.style.transition = "0.3s all";
          tippy(info.el.parentNode, {
            content: comp2Dom(Tooltip, {
              props: {
                data: info.event.extendedProps,
              },
            }),
            allowHTML: true, // html
            interactive: true, // 
            arrow: false, // 
            delay: [300, null], // [, ]
          });
        },
      },
    };
  },

  methods: {
    // 
    qiehuanZhou(funcName) {
      const calendar = this.$refs.fullCalendar.getApi();

      calendar[funcName]();
    },
  },
};
</script>

