.demo-app-main {
  padding: 30px;
}

.width-all {
  left: 0% !important;
  right: 0% !important;
  z-index: 99999 !important;
}

.base {
  border-radius: 0px;
  border: none;
}

.base {
  border-radius: 0px;
  border: none;
}

.warning {
  background: #f8ecda;
  border-left: 3px solid #e6a23c;
}

.success {
  background: #e4fad1;
  border-left: 3px solid #67c23a;
}

.danger {
  background: #f9e3e2;
  border-left: 3px solid #f56c6c;
}

